<template>
  <div id="dashboard">
    <div v-if="loading" class="progress">
      <div class="indeterminate"></div>
    </div>
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Employees</h4>
      </li>
      <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
        <div class="chip">{{employee.dept}}</div>
        {{employee.employee_id}}: {{employee.name}}

        <router-link
          class="secondary-content"
          @click.native="setLoading"
          v-bind:to="{name: 'view-employee',params: {employee_id: employee.employee_id}}">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<style>
  .progress {
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 0;
  }
</style>


<script>

import db from '@/firebase/firebaseInit'

export default {
  name: 'Dashboard',
  data() {
    return {
      loading: false,
      employees: []
    }
  },
  beforeRouteEnter(to, from, next) {
    db.collection('employees').orderBy('dept').get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => vm.setData(doc))
        })
      })
  },
  beforeRouteUpdate(to, from, next) {
    this.employees = []
    db.collection('employees').orderBy('dept').get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.setData(doc)
          next()
        })
      })
  },
  methods: {
    setData(employee) {
      const data = {
        'id': employee.id,
        'employee_id': employee.data().employee_id,
        'name': employee.data().name,
        'dept': employee.data().dept,
        'position': employee.data().position
      }
      this.employees.push(data)
    },
    setLoading() {
      this.loading = true
    }
  }
}
</script>



