<template>
  <div id="edit-employee">
    <div v-if="loading" class="progress">
      <div class="indeterminate"></div>
    </div>
    <h3>Edit Employee</h3>
    <div class="row">
      <form @submit.prevent="updateEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input disabled type="text" :value="employee_id" required>
            <label class="active">Employee ID#</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
            <label class="active">Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="dept" required>
            <label class="active">Department</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="position" required>
            <label class="active">Position</label>
          </div>
        </div>
        <router-link @click.native="setLoading" class="btn grey" to="/">Cancel</router-link>
        <button @click="setLoading" type="submit" class="btn">Submit</button>
      </form>
    </div>
  </div>
</template>

<style>
  .progress {
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 0;
  }
</style>

<script>

import db from '@/firebase/firebaseInit'

export default {
  name: 'EditEmployee',
  data() {
    return {
      loading: false,
      employee_id: null,
      name: null,
      dept: null,
      position: null
    }
  },
  beforeRouteEnter(to, from, next) {
    db.collection('employees')
      .where('employee_id', '==', to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => vm.setData(doc.data()))
        })
      }).catch(err => console.log(err))
  },
  beforeRouteUpdate(to, from, next) {
    this.employee_id = null
    this.name = null
    this.dept = null
    this.position = null
    db.collection('employees')
      .where('employee_id', '==', to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.setData(doc.data())
          next()
        })
      }).catch(err => console.log(err))
  },
  methods: {
    setLoading() {
      this.loading = true
    },
    setData(employee) {
      this.employee_id = employee.employee_id
      this.name = employee.name
      this.dept = employee.dept
      this.position = employee.position
    },
    updateEmployee() {
      db.collection('employees')
      .where('employee_id', '==', this.$route.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.update({
            name: this.name,
            dept: this.dept,
            position: this.position
          })
          .then(() => {
            this.$router.push({name: 'view-employee', params: {employee_id: this.employee_id}})
          })
        })
      }).catch(err => console.log(err))
    }

  }
}
</script>



