<template>
  <div id="view-employee">
    <div v-if="loading" class="progress">
      <div class="indeterminate"></div>
    </div>
    <ul class="collection with-header">
      <li class="collection-header"><h4>{{name}}</h4></li>
      <li class="collection-item">Employee ID#: {{employee_id}}</li>
      <li class="collection-item">Department: {{dept}}</li>
      <li class="collection-item">Position: {{position}}</li>
    </ul>
    <router-link @click.native="setLoading" to="/" class="btn grey">Back</router-link>
    <button @click="deleteEmployee" class="btn red">Delete</button>

    <div class="fixed-action-btn">
      <router-link @click.native="setLoading" v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn-floating btn-large red">
        <i class="fa fa-pencil-alt"></i>
      </router-link>
    </div>

  </div>
</template>

<style>
  .progress {
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 0;
  }
</style>

<script>

import db from '@/firebase/firebaseInit'

export default {
  name: 'ViewEmployee',
  data() {
    return {
      loading: false,
      employee_id: null,
      name: null,
      dept: null,
      position: null
    }
  },
  beforeRouteEnter(to, from, next) {
    db.collection('employees')
      .where('employee_id', '==', to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => vm.setData(doc.data()))
        })
      }).catch(err => console.log(err))
  },
  beforeRouteUpdate(to, from, next) {
    this.employee_id = null
    this.name = null
    this.dept = null
    this.position = null
    db.collection('employees')
      .where('employee_id', '==', to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.setData(doc.data())
          next()
        })
      }).catch(err => console.log(err))
  },
  methods: {
    setData(employee) {
      this.employee_id = employee.employee_id
      this.name = employee.name
      this.dept = employee.dept
      this.position = employee.position
    },
    deleteEmployee() {
      if(confirm('Are you sure?')) {
        db.collection('employees')
      .where('employee_id', '==', this.$route.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.delete()
          this.$router.push('/')
        })
      })
      }
    },
    setLoading() {
      this.loading = true
    }
  }
}
</script>



